import React, { useState, useEffect } from "react";
import "./Image.Grid.css";
import golf from "./img/golf.jpg";

function ImageGrid() {
    const [cardWidthIndex, setCardWidthIndex] = useState(2);
    const cardWidthArray = ["50%", "40%", "30%", "20%", "15%"];
    let evCache = new Array();
    let prevDiff = -1;
    // cardWidthIndex = 2;

    const removeEvent = (e) => {
        const index = evCache.findIndex(
            (cache) => cache.pointerId === e.pointerId,
        );
        evCache.splice(index, 1);
    };

    const pointerDownHandler = (e) => {
        evCache.push(e);
    };

    const pointerMoveHandler = (e) => {
        const cards = document.querySelector(".cards");
        const index = evCache.findIndex(
            (cache) => cache.pointerId === e.pointerId,
        );
        evCache[index] = e;

        if (evCache.length === 2) {
            const curDiff = Math.abs(evCache[0].clientX - evCache[1].clientX);

            if (prevDiff > 0) {
                if (curDiff > prevDiff) {
                    if (cardWidthIndex > 0) {
                        setCardWidthIndex(cardWidthIndex - 1);
                    }
                }
                if (curDiff < prevDiff) {
                    if (cardWidthIndex < 4) {
                        setCardWidthIndex(cardWidthIndex + 1);
                    }
                }
            }

            prevDiff = curDiff;
        }
    };

    const pointerUpHandler = (e) => {
        removeEvent(e);
        if (evCache.length < 2) {
            prevDiff = -1;
        }
    };

    // useEffect(() => {
    //     document.querySelector(
    //         ".cards",
    //     ).style.gridTemplateColumns = `repeat(auto-fill, minmax(${cardWidthArray[cardWidthIndex]}, 1fr))`;
    // }, [cardWidthIndex]);

    return (
        <div
            className="touch-area"
            onPointerDown={pointerDownHandler}
            onPointerMove={pointerMoveHandler}
            onPointerUp={pointerUpHandler}
            onPointerCancel={pointerUpHandler}
            onPointerOut={pointerUpHandler}
            onPointerLeave={pointerUpHandler}
        >
            version 8
            <div
                className="cards"
                style={{
                    gridTemplateColumns: `repeat(auto-fill, minmax(${cardWidthArray[cardWidthIndex]}, 1fr))`,
                }}
            >
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div>
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
                <div className="card-area">
                    <img class="card-img" src={golf} alt="golf" />
                </div>
            </div>
        </div>
    );
}

export default ImageGrid;
